<template>
    <button @click="disparaAcao()" :class="estiloDoBotao" :type="tipo"> {{rotulo}} </button>

</template>

<script> 

    export default{
        props: {
            tipo: {
                required: true,
                type: String
            },
            rotulo: {
                required: true,
                type: String
            },
            confirmacao: Boolean,

            estilo: String
            
        },



        methods: {   
            disparaAcao(){
                if (this.confirmacao) {
                    if (confirm('confirma a operação? ')){
                        this.$emit('botaoAtivado');
                    }
                    return;
                }
                this.$emit('botaoAtivado');

            }


        },
        computed:{
            estiloDoBotao(){
                if(this.estilo == 'padrao' || !this.estilo) return 'botao botao-padrao';
                if(this.estilo == 'perigo') return 'botao botao-perigo';
            }
        }

    }



</script>

<style>

.botao{
    display: inline-block;
    padding: 10px;
    border-radius: 3px;
    margin: 10px;
    font-size: 1.2rem;
}

.botao-perigo{
    background: firebrick;
    color: azure;
}

.botao-padrao{
    background: darkcyan;
    color: azure;
}



</style>



